
import { createItem } from '@/api/mobile/children'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

const genderOptions = [
{ key: 'male', value: '男' },
{ key: 'female', value: '女' }
]
export default {
name: 'CreateChild',
components: {
Group,
XInput,
PopupPicker,
PopupRadio,
Datetime
},
data() {
return {
genderOptions,
temp: {
child_name: undefined,
gender: undefined,
birthday: undefined,
user_id: this.$route.query.user_id,
doctor_id: this.$route.query.doctor_id
},
child_id: undefined,
title1: '性别'
}
},
methods: {
blur(name) {
this.$refs[name].scrollIntoView(false)
},
handleClickSave() {
console.log('temp => ', this.temp)
if (this.temp.child_name === undefined || this.temp.child_name === '') {
return
}
createItem(this.temp).then(response => {
this.child_id = response.data.childId
this.$router.push({ path: '/bind_success', query: { child_id: this.child_id }})
})

},
onChange(val) {
console.log('val change', val)
},
onShow() {
console.log('on show')
},
onHide(type) {
console.log('on hide', type)
},
onShowDate() {
console.log('on show')
},
onHideDate(type) {
console.log('on hide', type)
},
change(value) {
console.log('change', value)
}
}
}



